.wrapper {
    position: relative;
    min-height: 90vh;
    padding-top: 15rem;
}

.image-wrap {
    padding: 1rem;
    display: flex;
    flex-direction: column;
    gap: 50vh;

}

.text-wrap {
    position: sticky;
    top: 80%;
    left: 10%;
}

.text-wrap .text {
    display: none;
}

.imgDiv {
    display: flex;
    gap: 5rem;
    min-height: 80vh;
    z-index: 0;
    flex-wrap: wrap;
    overflow: visible;
    padding: 1rem 2rem;
}


.isShown {
    z-index: -1;
    display: block !important;
    font-family: var(--font2);
    font-size: 8rem;
    font-weight: 800;
    position: relative;
}

.imgBox {
    height: 15rem;
    width: 30rem;
    position: relative;
    align-self: center;
}

.imgBox a{
    height: inherit;
    width: inherit;
    position: inherit;
}

.imgDiv img {
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.secondImg div:nth-child(1),.secondImg div:nth-child(3),.first div:nth-child(2),.first div:nth-child(3){
    height: 30rem;
}

.secondImg div:nth-child(1) img,.secondImg div:nth-child(3) img ,.first div:nth-child(2) img,.first div:nth-child(3) img{
    object-fit: contain;
}

.secondImg div:nth-child(2) , .lastImg div:nth-child(1) , .first div:nth-child(1){
    height: 30rem;
    flex-grow: 1;
}

.circleContainer{
    position: relative;
    margin-top: 8rem;
    z-index: 10;
}

.circle{
    background-color: #09090a;
    width: 140%;
    height: 900%;
    left: -20%;
    position: absolute;
    z-index: 1;
    border-radius: 0% 0% 50% 50%;
}
